import React, { useState, useEffect } from "react";
import { useCart } from "./context/CartProvider";
import { Button, Header, Image, Modal, Transition } from "semantic-ui-react";

export default function QuickShop({ product, openModal, setOpenModal }) {
  const { updateCart } = useCart();
  const [open, setOpen] = useState(false);
  const [visible, setVisible] = useState(false);

  //EXPIREMENTAL*** trying to reconfigure open modal state to be separate from this component
  //so that button to open modal is unhooked from transition comp. transition comp
  //is making button invisible when button is contained in modal trigger prop

  const quickAddToCart = () => {
    product["quantity"] = 1;
    updateCart(product);
    setOpenModal(false);
  };

  // useEffect(() => {
  //   if (openModal === true) {
  //     setVisible(true);
  //   } else {
  //     setVisible(false);
  //   }
  // }, [openModal]);

  return (
    <Transition animation="fade" duration={500} visible={openModal}>
      <Modal
        id={product.id}
        onClose={() => setOpenModal(false)}
        onOpen={() => setOpenModal(true)}
        open={openModal}
      >
        <Modal.Content image>
          <Image size="medium" src={product.image} wrapped />
          <Modal.Description>
            <Header>{product.title}</Header>
            <p>{product.description}</p>
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button color="black" onClick={() => setOpenModal(false)}>
            Continue Shopping
          </Button>
          <Button
            content="Add To Cart"
            labelPosition="right"
            icon="shopping cart"
            onClick={(e) => quickAddToCart()}
            positive
          />
        </Modal.Actions>
      </Modal>
    </Transition>
  );
}
